<template>
  <div class="navbar-container" ref="navbarContainer">
    <cube-tab-bar v-model="curPage" @change="changeHandler">
      <cube-tab v-for="(item, index) in tabs" :label="item.label" :key="index">
        <!-- nameicon -->
        <div slot="icon" :class="item.icon"></div>
        <!--  -->
        {{ item.label }}
        <div class="shoppingCartNum" v-show="item.label === ''">
          {{ total }}
          <span v-show="total >= 999">+</span>
        </div>
      </cube-tab>
    </cube-tab-bar>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Navbar",
  data() {
    return {
      curPage: "",
      tabs: [
        {
          label: "",
          icon: "cubeic-home"
        },
        {
          label: "",
          icon: "cubeic-tag"
        },
        {
          label: "",
          icon: "cubeic-mall"
        },
        {
          label: "",
          icon: "cubeic-person"
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["curCartData"]),
    total: function() {
      let sum = 0;
      for (let item of this.curCartData) {
        sum += item.count;
      }
      if (sum > 999) {
        sum = 999;
      }
      return sum;
    }
  },
  methods: {
    ...mapActions(["setShopCartPosition"]),
    changeHandler(label) {
      switch (label) {
        case "":
          this.$router.push("/");
          break;
        case "":
          this.$router.push("/categories");
          break;
        case "":
          this.$router.push("/shoppingcart");
          break;
        case "":
          this.$router.push("/my");
          break;
      }
    }
  },
  created() {},
  activated() {
    switch (this.$route.path) {
      case "/":
        this.curPage = "";
        break;
      case "/categories":
        this.curPage = "";
        break;
      case "/shoppingcart":
        this.curPage = "";
        break;
      case "/my":
        this.curPage = "";
        break;
    }
  },
  deactivated() {
    //
    this.$nextTick(function() {
      let shopCartPosition = document
        .getElementsByClassName("cubeic-mall")[0]
        .getBoundingClientRect();

      //vuex
      this.setShopCartPosition(shopCartPosition);
    });
  }
};
</script>

<style lang="stylus" scoped>
.navbar-container
  position fixed
  bottom 0
  left 0
  width 100%
  z-index 1000
  background-color white
  box-shadow: 0 0 10px 0 hsla(0,6%,58%,.6);
  font-size 18px
  .cube-tab-bar
    .cube-tab_active
      color orange
    .cube-tab
      position relative
      .shoppingCartNum
        position absolute
        top 0
        right 10px
        background-color orange
        width 25px
        height 25px
        font-size 12px
        border-radius 50%
        color white
        display flex
        justify-content center
        align-items center
</style>
